import * as React from 'react';
import { Button, Text, View } from 'react-native';
import { WebView } from 'react-native-webview';
import { htmlTemplate } from './htmlTemplate';
import type { ChatnelsWidgetProps, ChatnelsWidgetConfig } from './types';

export const ChatnelsWidget: React.FC<ChatnelsWidgetProps> = ({
  orgDomain,
  serviceProvider = 'chatnels.com',
  sessionToken,
  viewData,
  onChatnelsEvent,
  onReady,
  onRequestSession,
  onError,
}) => {
  const webviewRef = React.useRef<WebView>(null);
  const [webviewKey, setWebviewKey] = React.useState<number>(1);
  const [source, setSource] = React.useState<string>('');
  const [savedSessionToken, setSavedSessionToken] =
    React.useState(sessionToken);
  const [savedViewData, setSavedViewData] =
    React.useState<ChatnelsWidgetConfig>();
  const [hasError, setHasError] = React.useState(false);

  const injectEmbedData = React.useCallback(
    (embedData: ChatnelsWidgetConfig) => {
      if (webviewRef.current) {
        const { type, data, options, colorScheme } = embedData;
        const run = `
            if(window.ChatnelsClient) {
              window.ChatnelsClient.showView({
                type: "${type}",
                data: ${JSON.stringify(data)},
                options: ${JSON.stringify(options)},
                colorScheme: ${JSON.stringify(colorScheme)},
              });
            }
            true;
          `;
        webviewRef.current.injectJavaScript(run);
      }
    },
    []
  );

  const handleError = React.useCallback(
    (e: any) => {
      setHasError(true);

      if (onError) {
        onError(e);
      }
    },
    [onError]
  );

  const handleMessage = React.useCallback(
    (event) => {
      try {
        const { type, data } = JSON.parse(event.nativeEvent.data);

        if (type === 'loadScriptError') {
          handleError(new Error(data));
        } else if (type === 'reAuth') {
          if (onRequestSession) {
            onRequestSession();
          }
        } else if (type === 'app:ready') {
          if (savedViewData) {
            injectEmbedData(viewData);
          }

          if (onReady) {
            onReady();
          }
        } else if (onChatnelsEvent) {
          onChatnelsEvent(type, data);
        }
      } catch (e) {}
    },
    [
      handleError,
      injectEmbedData,
      savedViewData,
      viewData,
      onChatnelsEvent,
      onReady,
      onRequestSession,
    ]
  );

  // const handleRenderError = React.useCallback(
  //   (errorDomain, errorCode, errorDesc) => {
  //     return <></>;
  //   },
  //   []
  // );

  const handleReload = React.useCallback(() => {
    setHasError(false);
    setWebviewKey(webviewKey + 1);
  }, [webviewKey]);

  React.useEffect(() => {
    if (orgDomain) {
      setSource(
        htmlTemplate(orgDomain, serviceProvider, sessionToken, viewData)
      );
    }
  }, []);

  React.useEffect(() => {
    if (webviewRef.current) {
      if (sessionToken !== savedSessionToken) {
        setSavedSessionToken(sessionToken);
        const run = `
          if(window.ChatnelsClient) {
            window.ChatnelsClient.updateSessionToken("${sessionToken}");
          }
          true;
        `;
        webviewRef.current.injectJavaScript(run);
      }
    }
  }, [sessionToken, savedSessionToken]);

  React.useEffect(() => {
    try {
      if (webviewRef.current) {
        if (JSON.stringify(savedViewData) !== JSON.stringify(viewData)) {
          setSavedViewData(viewData);
          injectEmbedData(viewData);
        }
      }
    } catch (e) {}
  }, [injectEmbedData, viewData, savedViewData]);

  return (
    <View style={{ flex: 1 }}>
      <WebView
        key={webviewKey}
        ref={webviewRef}
        source={{ html: source, baseUrl: 'https://local.chatnels.com/' }}
        style={{ flex: 1 }}
        bounces={true}
        decelerationRate={'fast'}
        domStorageEnabled={true}
        injectedJavaScriptForMainFrameOnly={true}
        injectedJavaScriptBeforeContentLoadedForMainFrameOnly={true}
        javaScriptEnabled={true}
        mixedContentMode={'always'}
        originWhitelist={['*']}
        overScrollMode={'never'}
        scrollEnabled={false}
        thirdPartyCookiesEnabled={true}
        onMessage={handleMessage}
        onError={handleError}
      />
      {hasError && (
        <View
          style={{
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
            position: 'absolute',
            flex: 1,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center',
          }}
        >
          <Text>Unable to connect to service. Please try again later.</Text>
          <Button title="Retry" onPress={handleReload} />
        </View>
      )}
    </View>
  );
};
